.svg{
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
}

.svg-container{
  position: relative;
  width: 100%;
  height: 400px;
}

#pupil{
  transform-origin: center;

}


#mail{
  writing-mode: vertical-lr;
}

.others::before {
  content: '';
  width: 0;
  grid-row: 1 / 1;
  grid-column: 1 / 1;
}

.others > *:first-child{
  grid-row: 1 / 1;
  grid-column: 1 / 1;
}

html {
  scroll-behavior: smooth;
  scroll-padding-top: 120px; /* If you need scroll offset try this magic! */
}